<template>
    <div>
        <share-buttons orientation="vertical"></share-buttons>

        <header-main></header-main>

        <main>
            <div class="light-gray">
                <div class="intro">
                    <posts :category="$route.params.category"></posts>
                </div>
            </div>
        </main>

        <base-footer></base-footer>
    </div>
</template>

<script>
    import HeaderMain from '@/components/HeaderMain'
    import Posts from '@/components/Posts'
    import BaseFooter from '@/components/BaseFooter'
    import ShareButtons from '@/components/ShareButtons'

    export default {
        name: 'Category',

        methods: {
            validateCategory: function() {
                let found = false;

                for (let i = 0; i < this.$root.$children[0].categories.length; i++) {
                    if (this.$root.$children[0].categories[i].alias === this.$route.params.category) {
                        found = true;
                        break;
                    }
                }

                if (!found) {
                    this.$router.push('/');
                    return false;
                }

                return true;
            }
        },

        created: function () {
            this.validateCategory();
        },

        mounted: function () {
            document.title = this.$root.$children[0].getCategoryName(this.$route.params.category) + ' - ' + this.$root.$children[0].title;
        },

        watch: {
            '$route' (to, from) {
                this.validateCategory();
                document.title = this.$root.$children[0].getCategoryName(this.$route.params.category) + ' - ' + this.$root.$children[0].title;
            }
        },

        components: {
            HeaderMain,
            Posts,
            BaseFooter,
            ShareButtons
        }

    }
</script>


