<template>
    <div class="ep">

        <img :src="image" :alt="title" hidden>

        <header class="header ep-header">

            <router-link to="/" class="logo logo--compact">George Shinakrev blog</router-link>

            <div class="ep-header__title" :title="title">
                {{ title }}
            </div>
            <div class="ep-header__buttons">

                <div>
                    <a v-if="!error" href="#" @click.prevent="toggleSharePopup()" class="ep-header__button js-share-button">
                        <i class="icon icon--share"></i>
                        <span>Поделиться</span>
                    </a>

                    <div v-show="share" class="ep-header__share">
                        <a :href="'https://vk.com/share.php?title=' + title + '&url=' + getUrl()" target="_blank" class="ep-header__share-item ep-header__share-item--vk"></a>
                        <a :href="'https://www.facebook.com/sharer/sharer.php?u=' + getUrl()" target="_blank" class="ep-header__share-item ep-header__share-item--facebook"></a>
                        <a :href="'https://twitter.com/intent/tweet?text=' + title + '&url=' + getUrl()" target="_blank" class="ep-header__share-item ep-header__share-item--twitter"></a>
                        <a :href="'http://telegram.me/share/url?url=' + getUrl()" target="_blank" class="ep-header__share-item ep-header__share-item--telegram"></a>
                    </div>
                </div>

                <a v-for="(url, alias) in buttons" :href="url" target="_blank" class="ep-header__button">
                    <i :class="'icon icon--' + alias"></i>
                    <span>{{ getButtonTitle(alias) }}</span>
                </a>

                <router-link to="/" class="ep-header__button">
                    <i class="icon icon--home"></i>
                    <span>Смотреть все посты</span>
                </router-link>

            </div>
        </header>

        <main class="ep-main">

            <div v-if="loading">
                Загрузка ...
            </div>

            <div v-if="error" class="ep-error">
                <span>{{ error }}.</span>
                &nbsp; <router-link to="/">Вернуться на главую страницу.</router-link>
            </div>

            <iframe v-else :src="frame" frameborder="0"></iframe>
        </main>

    </div>
</template>

<script>
    export default {
        name: "EntryPostNew",
        
        data() {
            return {
                loading: true,
                error: null,

                image: '',
                frame: '',
                title: '',
                buttons: {},

                share: false
            }
        },

        methods: {

            getUrl: function () {
                return window.location.href;
            },

            toggleSharePopup: function () {
                this.share = !this.share;
            },

            getButtonTitle: function(alias) {
                let title = alias;

                switch (alias) {

                    case 'download':
                        title = 'Загрузить';
                        break;

                    case 'github':
                        title = 'GitHub';
                        break;

                    case 'faq':
                        title = 'Вопросы/ответы';
                        break;

                    case 'buy':
                        title = 'Приобрести';
                        break;

                    default:
                        break;
                }

                return title;
            }

        },
        
        created: function() {

            if (process.env.NODE_ENV === 'development') {

                this.loading = false;
                this.error = null;

                this.image = '';
                this.title = 'Игра в города';
                this.frame = 'http://gkshi.ucoz.net/cities';
                this.buttons = {
                    github: 'https://github.com/gkshi/cities'
                };

                document.title = 'Игра в города - GS Blog';

            }
            else {

                this.$http.get(this.$root.$children[0].domain + 'posts/' + this.$route.params.id + '/index.json').then(response => {
                    // success response
                    this.loading = false;
                    this.error = null;

                    this.image = response.body.image;
                    this.title = response.body.title;
                    this.frame = response.body.frame;
                    this.buttons = response.body.buttons;

                    document.title = response.body.title + ' - ' + this.$root.$children[0].title;

                }, response => {
                    // error callback
                    this.loading = false;
                    this.error = response.status + ' ' + response.statusText;
                    //this.error = null;
                    document.title = response.body.title + ' - ' + this.$root.$children[0].title;
                });

            }

        },

        mounted: function() {
            let _this = this;

            // Клик по фрейму, закрытие шеринга
            document.querySelector('iframe').addEventListener('load', function() {
                this.contentDocument.querySelector('body').addEventListener('click', function() {
                    _this.share = false;
                });
            });

            // Клик по телу, закрытие шеринга
            document.querySelector('body').addEventListener('click', function(e) {

                let el = e.target,
                    re = false,
                    body = document.querySelector('body');

                while (el !== body) {
                    if (el.classList.contains('js-share-button')) {
                        re = true;
                        break;
                    }
                    el = el.parentNode;
                }

                if (!re) {
                    _this.share = false;
                }

            });
        }
        
    }
</script>


